import fs from "fs"
import path from "path"

import QuoteFormPortal from "@/components/quote-form"

const PLACEHOLDER = '<div id="quote-form-host"></div>'
const bodyPath = path.join(process.cwd(), "data/metalshanxi-body.html")
const rawHtml = fs.readFileSync(bodyPath, "utf-8")

const normalizedHtml = rawHtml
  .replace(/tel:\+8618662678180/g, "tel:+8615679222017")
  .replace(/\+8618662678180/g, "15679222017")
  .replace(/sales@lynsteel\.com/gi, "1011300569@qq.com")
  .replace(/sales@lnysteel\.com/gi, "1011300569@qq.com")
  .replace(/href="https:\/\/www\.metalshanxi\.com\/contact-us\/"/g, 'href="#quote-form"')
  .replace(/href="https:\/\/demo\.7iquid\.com\/steeler\/get-in-touch"/g, 'href="#quote-form"')

const htmlWithPlaceholder = normalizedHtml.replace(
  /<form class="elementor-form"[\s\S]*?<\/form>/,
  PLACEHOLDER,
)

const PAGE_HTML = htmlWithPlaceholder

export default function Home() {
  return (
    <>
      <div dangerouslySetInnerHTML={{ __html: PAGE_HTML }} />
      <QuoteFormPortal />
    </>
  )
}
